<template>
  <div id="home">
    <div class="header">
      <div class="header__hamburger">
        <a href="#" @click.prevent='toggle_category()'>
          <img src="../assets/icon/main_menu_hamburger.png" alt=""
          class="header__hamburger__folded">
          <img src="../assets/icon/close_menu.png" alt=""
          class="header__hamburger__cross d-none">
        </a>
      </div>
      <div class="header__nav">
        <div class="header__nav__leftArrow">
          <a href="#" @click.prevent='animate_scroll("header__nav", -1)'>
            <img src="../assets/icon/main_menu_arrow_left.png" alt="">
          </a>
        </div>
        <ul class="header__nav__list"
        @mouseenter="bind_scroll_event('header__nav')"
        @mouseleave="unbind_scroll_event()"
        >
          <li class="header__nav__list__item"
          v-for="(child, index) in database.header_nav"
          :key="index"
          >
            <a href="#" @click.prevent='go_page(`front/${child.path}`)'>
              <span class="chinese">{{child.chinese}}</span>
              <span class="english">{{child.english}}</span>
            </a>
          </li>
        </ul>
        <div class="header__nav__rightArrow">
          <a href="#" @click.prevent='animate_scroll("header__nav", 1)'>
            <img src="../assets/icon/main_menu_arrow.png" alt="">
          </a>
        </div>
      </div>
      <ul class="header__sharelinks">
        <li class="header__sharelinks__item">
          <a :href="this.contact_info.facebook.link">
            <img src="../assets/icon/main_menu_fb.png" alt="" class="rwdM">
            <img src="../assets/icon/facebook_icon.png" alt="" class="rwdL">
          </a>
        </li>
        <li class="header__sharelinks__item">
          <a :href="this.contact_info.instagram.link">
            <img src="../assets/icon/main_menu_ig.png" alt="" class="rwdM">
            <img src="../assets/icon/instagram_icon.png" alt="" class="rwdL">
          </a>
        </li>
        <li class="header__sharelinks__item">
          <a :href="this.contact_info.line.link">
            <img src="../assets/icon/main_menu_line.png" alt="" class="rwdM">
            <img src="../assets/icon/line_icon.png" alt="" class="rwdL">
          </a>
        </li>
      </ul>
    </div>
    <div class="category d-none">
      <div class="category__leftArrow"
      @click.prevent='animate_scroll("category", -1)'>
        <a href="#">
          <img src="../assets/icon/main_menu_arrow_left.png" alt="">
        </a>
      </div>
      <ul class="category__list"
      @mouseenter="bind_scroll_event('category')"
      @mouseleave="unbind_scroll_event()">
        <li class="category__list__item"
        v-for="(category, index) in database.product_category"
        :key="index">
          <a href="#"
          @click.prevent="go_page('front/onlinestore/'+category)"
          >{{category}}</a>
        </li>
      </ul>
      <div class="category__rightArrow"
      @click.prevent='animate_scroll("category", 1)'>
        <a href="#">
          <img src="../assets/icon/main_menu_arrow.png" alt="">
        </a>
      </div>
    </div>
    <div class="banner">
      <ul class="banner__bgimg">
        <li class="banner__bgimg__item"
        v-for="(product, index) in database.product_on_interval"
        :key="index">
          <transition name='fade'>
            <img :src="product.img" alt="" v-if="product.is_shown">
          </transition>
        </li>
      </ul>
      <div class="banner__logo">
        <img src="../assets/banner_logo.png" alt="">
      </div>
      <div class="banner__onlinestore">
        <img src="../assets/icon/banner_store_link.png" alt="">
      </div>
      <div class="banner__link">
        <a href="#" @click.prevent='go_page(`front/onlinestore/all/${rendor.banner_link}`)'></a>
      </div>
      <ul class="banner__bgimgnavlist">
        <li class="banner__bgimgnavlist__item"
        v-for="(product, index) in database.product_on_interval"
        :key="index">
          <a href="#" @click.prevent='change_bgimg(index)'>
            <div :class="{active: product.is_shown}"></div>
          </a>
        </li>
      </ul>
    </div>
    <div class="footer">
      <div class="footer__logo">
        <img src="../assets/footer_logo.png" alt="">
      </div>
      <div class="footer__copyright">
        <span>COPYRIGHT GOODAFTER © ALL RIGHTS RESERVED.</span>
      </div>
      <ul class="footer__contact">
        <li class="footer__contact__item">
          <a :href="this.contact_info.tel.link">
            {{this.contact_info.tel.text}}
          </a>
        </li>
        <li class="footer__contact__item rwdL">
          |
        </li>
        <li class="footer__contact__item">
          <a :href="this.contact_info.line.link">
            {{this.contact_info.line.text}}
          </a>
        </li>
        <li class="footer__contact__item rwdL">
          |
        </li>
        <li class="footer__contact__item">
          <a :href="this.contact_info.mail.link">
            {{this.contact_info.mail.text}}
          </a>
        </li>
        <li class="footer__contact__item rwdL">
          |
        </li>
        <li class="footer__contact__item">
          {{this.contact_info.address.text}}
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import $ from 'jquery';

export default {
  props: ['products', 'contact_info'],
  data() {
    return {
      bgimg_interval: null,
      database: {
        header_nav: [
          // {
          //   path: '',
          //   chinese: '',
          //   english: '',
          // },
        ],
        contact_info: {
          // facebook: {
          //   link: '',
          //   text: '',
          // }.
        },
        product_category: [
          // '',
        ],
        product_on_interval: [
          // {
          //   img: '',
          //   link: '',
          //   is_shown: true,
          // },
        ],
      },
      rendor: {
        banner_link: '',
      },
    };
  },
  methods: {
    initialize_database() {
      const vm = this;
      const copyRoutes = JSON.parse(JSON.stringify(vm.$router.options.routes));
      const copyProductArray = JSON.parse(JSON.stringify(vm.products));
      const copyContactObj = JSON.parse(JSON.stringify(vm.contact_info));

      // header_nav
      const routeFrontArray = copyRoutes
        .find((route) => route.name === 'Front');
      routeFrontArray.children.forEach((child) => {
        const tempObj = {};
        tempObj.path = child.path;
        tempObj.chinese = child.chinese;
        tempObj.english = child.english;
        if (child.on_header_nav) {
          vm.database.header_nav.push(tempObj);
        }
      });

      // contact_info
      vm.database.contact_info = copyContactObj;

      // product_category
      const categoryArray = copyProductArray.map((product) => product.category);
      const categoryArrayFlat = categoryArray.flat(9);
      vm.database.product_category = categoryArrayFlat
        .filter((element, index, arr) => arr.indexOf(element) === index);

      // product_on_interval
      const filterArray = copyProductArray
        .filter((product) => product.home_banner.on_banner === true);
      for (let index = 0; index < filterArray.length; index += 1) {
        const newObj = {};
        newObj.img = filterArray[index].home_banner.img;
        newObj.link = filterArray[index].id;
        newObj.is_shown = false;
        vm.database.product_on_interval.push(newObj);
      }
      vm.database.product_on_interval[0].is_shown = true;
      vm.rendor.banner_link = vm.database.product_on_interval[0].link;
    },
    go_page(pageString) {
      this.$router.push(`/${(pageString === 'front/onlinestore') ? 'front/onlinestore/all' : pageString}`);
    },
    animate_slide_fadein(elementString) {
      const element = document.querySelector(`.${elementString}`);
      $(element).toggleClass('animate-slide-hidden');
    },
    toggle_category() {
      const folded = document.querySelector('.header__hamburger__folded');
      const cross = document.querySelector('.header__hamburger__cross');
      const category = document.querySelector('.category');
      $(folded).toggleClass('d-none');
      $(cross).toggleClass('d-none');
      $(category).toggleClass('d-none');
      setTimeout(() => {
        this.animate_slide_fadein('category__list');
        this.toggle_arrow('category');
      }, 1);
    },
    set_bgimg_interval() {
      const vm = this;
      vm.bgimg_interval = setInterval(() => {
        const whichItemIsShown = vm.database.product_on_interval
          .findIndex((element) => element.is_shown === true);
        const arraylength = vm.database.product_on_interval.length;
        vm.database.product_on_interval[whichItemIsShown].is_shown = false;
        if (whichItemIsShown === arraylength - 1) {
          vm.database.product_on_interval[0].is_shown = true;
          vm.rendor.banner_link = vm.database.product_on_interval[0].link;
        } else {
          vm.database.product_on_interval[whichItemIsShown + 1].is_shown = true;
          vm.rendor.banner_link = vm.database.product_on_interval[whichItemIsShown + 1].link;
        }
      }, 8000);
    },
    start_bgimg_interval() {
      this.set_bgimg_interval();
    },
    end_bgimg_interval() {
      clearInterval(this.bgimg_interval);
    },
    change_bgimg(bgimgIndex) {
      const vm = this;
      this.end_bgimg_interval();
      const whichItemIsShown = vm.database.product_on_interval
        .findIndex((element) => element.is_shown === true);
      vm.database.product_on_interval[whichItemIsShown].is_shown = false;
      vm.database.product_on_interval[bgimgIndex].is_shown = true;
      this.rendor.banner_link = vm.database.product_on_interval[bgimgIndex].link;
      this.set_bgimg_interval();
    },
    toggle_arrow(elementString) {
      const leftArrow = document.querySelector(`.${elementString}__leftArrow`);
      const rightArrow = document.querySelector(`.${elementString}__rightArrow`);
      const list = document.querySelector(`.${elementString}__list`);
      const listLeft = $(list).scrollLeft();
      const listRight = listLeft + $(list).width();
      const items = document.querySelectorAll(`.${elementString}__list__item`);
      let itemsWidthSum = 0;
      items.forEach((item) => {
        itemsWidthSum += $(item).outerWidth(true);
      });
      itemsWidthSum = Math.floor(itemsWidthSum);
      if (listLeft <= 0) {
        $(leftArrow).fadeOut();
      } else {
        $(leftArrow).fadeIn();
      }
      if (listRight >= itemsWidthSum) {
        $(rightArrow).fadeOut();
      } else {
        $(rightArrow).fadeIn();
      }
    },
    animate_scroll(elementString, directionNum) {
      const element = document.querySelector(`.${elementString}__list`);
      if (directionNum > 0) {
        // 向右移動
        $(element).animate({
          scrollLeft: '+=50',
        }, 'fast', () => {
          this.toggle_arrow(elementString);
        });
      } else if (directionNum < 0) {
        // 向左移動
        $(element).animate({
          scrollLeft: '-=50',
        }, 'fast', () => {
          this.toggle_arrow(elementString);
        });
      }
    },
    bind_scroll_event(elementString) {
      $(window).bind('mousewheel', (e) => {
        // deltaY 正往右，負往左
        if (e.originalEvent.deltaY > 0) {
          this.animate_scroll(elementString, 1);
        } else if (e.originalEvent.deltaY < 0) {
          this.animate_scroll(elementString, -1);
        }
      });
    },
    unbind_scroll_event() {
      $(window).unbind('mousewheel');
    },
  },
  mounted() {
    document.title = 'GOODAFTER 日青方好';
    this.initialize_database();
    this.toggle_arrow('header__nav');
    this.toggle_arrow('category');
    this.animate_slide_fadein('header__nav__list');
    this.animate_slide_fadein('category__list');
    setTimeout(() => {
      this.animate_slide_fadein('header__nav__list');
    }, 10);
    this.start_bgimg_interval();
    $(window).resize(() => {
      this.toggle_arrow('header__nav');
      this.toggle_arrow('category');
    });
  },
};
</script>
